import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyAlphaNum, WindowComponent } from 'summer';
import { CLASS_NAME, ObsPadrao } from '../../../../models/dtos/ger/obsPadrao';
import AjusteInfoFiscalIS from '../../../components/ger/ajusteInfoFiscalIS/AjusteInfoFiscalIS';

interface ObservacaoPadraoEditViewProps extends EditViewProps<ObsPadrao> {}

const ObservacaoPadraoEditView: FC<ObservacaoPadraoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<ObsPadrao>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 100;

  return (
    <WindowComponent title="Manutenção de Observação Padrão">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyAlphaNum(0, 6) } }}
              id="codigo"
              isPrependLabel={true}
              label="Código"
              labelWidth={defaultWidth}
              placeholder="Informe o Código"
              type={InputType.NUMBER}
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 255 } }}
              id="descricao"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a Descrição"
              type={InputType.TEXT_AREA}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <AjusteInfoFiscalIS
              formProps={formProps}
              id="ajusteInfoFiscal"
              tooltipProps={{ message: 'Código de Ajuste e Informações de Documentos Fiscais - Registro C197 Sped Fiscal.' }}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ObservacaoPadraoEditView;
