import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/ger/ajusteInfoFiscal';

const AjusteInfoFiscalIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Código',
          },
          {
            dataField: 'descricao',
            text: 'Descrição',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'AjusteInfoFiscalSearchView',
      }}
      label="Código de Ajuste"
      placeholder="Informe o Código de Ajuste"
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default AjusteInfoFiscalIS;
